etween\n\n    .series-answers\n      @include flex-center\n      justify-content: space-between\n      width: 100px\n      margin: 20px auto 30px\n\n      .item\n        width: 18px\n        height: 18px\n        background-color: #eee\n        border-radius: 50%\n\n    .series--1\n      .item-1\n        background-color: $lightColor\n\n    .series--2\n      .item-1,\n      .item-2\n        background-color: $lightColor\n\n    .series--3\n      .item-1,\n      .item-2,\n      .item-3\n        background-color: $lightColor\n\n    .series--4\n      .item-1\n        background-color: $primaryColor\n\n    .series--5\n      .item-1,\n      .item-2\n        background-color: $primaryColor\n\n    .series--6\n      .item-1,\n      .item-2,\n      .item-3\n        background-color: $primaryColor\n\n    .series--7\n      .item-1\n        background-color: $darkColor\n\n    .series--8\n      .item-1,\n      .item-2\n        background-color: $darkColor\n\n    .series--9\n      .item-1,\n      .item-2,\n      .item-3\n        background-color: $darkColor\n\n    .word\n      font-size: 1.6em\n      font-weight: 500\n      margin-bottom: 30px\n\n      span\n        display: block\n\n      .word-english\n        font-size: 1.2em\n        font-weight: 600\n\n    .answers-btn\n      button:first-child\n        margin-right: 20px\n\n\n.result-score\n  max-width:100%\n  width: 700px\n  padding: 30px\n  background-color: #fff\n  border-radius: 10px\n  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.3)\n\n  .result-score__total\n    text-align: center\n    padding-bottom: 15px\n    margin-bottom: 25px\n    border-bottom: 1px solid $primaryColor\n\n    span\n      font-size: 1.2em\n\n  hr\n    margin: 25px 0\n    border-color: $primaryColor\n\n  .true-answer,\n  .false-answer\n    height: 265px\n    overflow-y: auto\n\n    &::-webkit-scrollbar\n      width: 7px\n      background-color: #eee\n\n    &::-webkit-scrollbar-thumb\n      background-color: $darkColor\n\n    h3\n      padding-top: 5px\n      margin-bottom: 15px\n\n      .count\n        color: $textColor\n        padding: 2px 10px 3px 10px\n        background-color: green\n        border-radius: 30px\n        \n\n    ul\n      font-size: 18px\n      list-style: none\n\n      li\n        @include flex-center\n        justify-content: start\n        margin-bottom: 8px\n\n        svg\n          margin-right: 10px\n          cursor: pointer\n\n        .dash\n          display: inline-block\n          margin: 0 5px\n\n  .btns\n    @include flex-center\n    justify-content: space-evenly\n    margin-top: 30px\n\n  .false-answer\n    h3\n      .count\n        background-color: #c00202\n\n@media (max-width: 449px)\n  .sprint-game .word-card,\n  .result-score\n    padding: 15px\n","@mixin cards-shadow {\n  -moz-box-shadow: 1px 5px 15px #555;\n  -webkit-box-shadow: 1px 5px 15px #555;\n  box-shadow: 1px 5px 15px #555;\n}\n\n@mixin cards-shadow-dark {\n  -moz-box-shadow: 1px 5px 15px #222;\n  -webkit-box-shadow: 1px 5px 15px #222;\n  box-shadow: 1px 5px 15px #222;\n}\n\n@mixin trans-fast {\n  -webkit-transition: 0.3s ease-out;\n  -moz-transition: 0.3s ease-out;\n  -o-transition: 0.3s ease-out;\n  transition: 0.3s ease-out;\n}\n\n@mixin trans-medium {\n  -webkit-transition: 0.5s ease-out;\n  -moz-transition: 0.5s ease-out;\n  -o-transition: 0.5s ease-out;\n  transition: 0.5s ease-out;\n}\n\n@mixin trans-slow {\n  -webkit-transition: 1s ease-out;\n  -moz-transition: 1s ease-out;\n  -o-transition: 1s ease-out;\n  transition: 1s ease-out;\n}\n\n@mixin icon-background {\n  background-size: contain;\n  background-position: center;\n  background-repeat: no-repeat;\n}\n\n@mixin flex-start {\n  display: flex;\n  justify-content: start;\n  align-items: center;\n}\n\n@mixin flex-center {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: center;\n  align-items: center;\n}\n\n@mixin flex-column-start {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n}\n\n@mixin flex-column-center {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n@mixin flex-between {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: space-between;\n  align-items: center;\n}\n\n@mixin flex-evenly {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: space-evenly;\n  align-items: center;\n}\n\n@mixin for-laptop {\n  @media (max-width: 1600px) {\n    @content;\n  }\n}\n\n@mixin for-laptop-down {\n  @media (max-width: 1200px) {\n    @content;\n  }\n}\n\n@mixin for-tablet {\n  @media (max-width: 720px) {\n    @content;\n  }\n}\n\n@mixin for-mobile {\n  @media (max-width: 600px) {\n    @content;\n  }\n}\n","$primaryColor: #4e97c2;\n$secondaryColor: #026aa7;\n$darkColor: #284968;\n$lightColor: #95bdd4;\n$backgroundColor: #f7f9ff;\n$backgroundDark: #111f2e;\n$textColor: #ecf7ff;\n$textDark: #000c18;\n$textLight: #8ad4ff;\n$white: #fff;\n$greyColor: #a2a6ad;\n$blackOpacity: rgba(0, 0, 0, 0.7);\n$blackOpacityHover: rgba(0, 0, 0, 0.8);\n$whiteOpacity: rgba(255, 255, 255, 0.8);\n$whiteOpacityHover: rgba(255, 255, 255, 0.9);\n\n$brandedColor1: #8b0061;\n$brandedColor2: #1b73ba;\n$brandedColor3: #f8d23c;\n$brandedColor4: #ff534d;\n$brandedColor5: #de8a00;\n$brandedColor6: #7478ae;\n$brandedColor7: #8cdd3f;\n\n$brandedColor1dark: #4b0034;\n$brandedColor2dark: #0d395e;\n$brandedColor3dark: #a38a27;\n$brandedColor4dark: #812a27;\n$brandedColor5dark: #9b6001;\n$brandedColor6dark: #4b4d70;\n$brandedColor7dark: #578b25;\n\n$brandedColor1light: #b90082;\n$brandedColor2light: #2189df;\n$brandedColor3light: #e6c337;\n$brandedColor4light: #ff534d;\n$brandedColor5light: #ff9d00;\n$brandedColor6light: #7478ae;\n$brandedColor7light: #8cdd3f;\n\n$mainCardWidth: 45%;\n"],"sourceRoot":""}]);
