ACA,2BAAA,CDkaF,4CACE,wDAAA,CAGF,4CACE,wDAAA,CAGF,4CACE,wDAAA","sourcesContent":["@import '../../utils/variables';\n@import '../../utils/_mixins.scss';\n\nmain .main-page {\n  width: 100%;\n  min-height: calc(100vh);\n  padding-left: 0;\n  padding-right: 0;\n\n  @include for-tablet {\n    padding-bottom: 30px;\n  }\n}\n\n.accordion {\n  width: 100%;\n  height: 100%;\n  display: flex;\n\n  @include for-tablet {\n    flex-direction: column;\n  }\n}\n\n.accordion .main-card {\n  width: calc((100% - $mainCardWidth) / 4);\n  height: 100%;\n  position: relative;\n  overflow: hidden;\n  cursor: pointer;\n  @include cards-shadow;\n  @include trans-medium;\n\n  @include for-tablet {\n    width: 100%;\n    height: 76px;\n    -moz-box-shadow: none;\n    -webkit-box-shadow: none;\n    box-shadow: none;\n  }\n\n  &_active {\n    width: $mainCardWidth;\n    cursor: inherit;\n\n    @include for-tablet {\n      width: 100%;\n      height: 100%;\n    }\n\n    & .main-card__img {\n      @include for-tablet {\n        display: flex;\n      }\n    }\n  }\n}\n\n.theme_dark .accordion .main-card {\n  @include cards-shadow-dark;\n}\n\n#accordion > .main-card_active .main-card__description,\n#accordion > .main-card_active .main-card__inner {\n  opacity: 0.9;\n  @include trans-medium;\n}\n\n#accordion > .main-card_active .main-card__inner {\n  @include flex-column-start;\n  color: $textColor;\n}\n\n#accordion > .main-card_active .main-card__link {\n  display: flex;\n}\n\n#accordion > .main-card_active .main-card__heading {\n  @include trans-fast;\n  opacity: 0;\n}\n\n#accordion > .main-card_active .advantages__wrapper {\n  display: none;\n}\n\n.accordion {\n  & .main-card_0 {\n    background-color: $brandedColor6;\n  }\n  & .main-card_1 {\n    background-color: $brandedColor3;\n  }\n  & .main-card_2 {\n    background-color: $brandedColor2;\n  }\n  & .main-card_3 {\n    background-color: $brandedColor1;\n  }\n  & .main-card_4 {\n    background-color: $brandedColor5;\n  }\n  & .main-card_5 {\n    background-color: $brandedColor7;\n  }\n}\n\n.theme_dark .accordion {\n  & .main-card_0 {\n    background-color: $brandedColor6dark;\n  }\n  & .main-card_1 {\n    background-color: $brandedColor3dark;\n  }\n  & .main-card_2 {\n    background-color: $brandedColor2dark;\n  }\n  & .main-card_3 {\n    background-color: $brandedColor1dark;\n  }\n  & .main-card_4 {\n    background-color: $brandedColor5dark;\n  }\n  & .main-card_5 {\n    background-color: $brandedColor7dark;\n  }\n}\n\nheader.header_card {\n  position: absolute;\n  top: 0;\n  left: 0;\n  background-color: transparent;\n  box-shadow: none;\n}\n\nheader.header_card .header__inner {\n  margin-top: 10px;\n  @include flex-start;\n\n  @include for-tablet {\n    margin-top: 4px;\n    padding-left: 10px;\n    padding-right: 10px;\n  }\n}\n\nheader.header_card .menu_small {\n  flex-grow: 0;\n  width: 48px;\n  height: 48px;\n  margin-right: 20px;\n}\n\nheader.header_card .menu_small button svg {\n  width: 24px;\n  height: 24px;\n}\n\nheader.header_card .login-container button {\n  padding: 0;\n}\n\n.main-card .theme-button__wrapper {\n  position: relative;\n}\n\n.main-card .theme-button__wrapper::before,\n.main-card .theme-button__wrapper::after {\n  content: '';\n  width: 1px;\n  height: 20px;\n  position: absolute;\n  top: 3px;\n  background-color: rgba(255, 255, 255, 0.6);\n}\n\n.main-card .theme-button__wrapper::before {\n  left: 0;\n}\n\n.main-card .theme-button__wrapper::after {\n  right: 0;\n}\n\n.accordion .main-card .main-card__heading {\n  margin-top: 80px;\n  padding: 10px;\n  opacity: 0.9;\n  text-transform: uppercase;\n  font-style: normal;\n  font-weight: bold;\n  letter-spacing: 1px;\n  font-size: 14px;\n  color: #444;\n  text-align: center;\n  background-color: $white;\n  text-shadow: -1px -1px 1px #ccc;\n  @include trans-fast;\n\n  @include for-tablet {\n    margin-top: 10px;\n    background-color: rgba(255, 255, 255, 0.4);\n    text-shadow: -1px -1px 1px rgb(163, 163, 163);\n  }\n}\n\n.theme_dark .accordion .main-card .main-card__heading {\n  background-color: $blackOpacity;\n  color: $textColor;\n  text-shadow: -1px -1px 1px #333;\n}\n\n#accordion .main-card_0 .main-card__heading {\n  @include for-tablet {\n    display: none;\n  }\n}\n\n.main-card .user-info {\n  display: none;\n}\n\n.accordion .main-card_active .user-info {\n  display: flex;\n}\n\n.theme_dark .about-project,\n.theme_dark .advantages {\n  color: $whiteOpacity;\n}\n\n#accordion .main-card_advantages.main-card_active {\n  @include for-tablet {\n    height: auto;\n  }\n}\n\n.main-card_advantages .main-card__inner {\n  padding: 0 6%;\n  display: none;\n\n  @include for-tablet {\n    padding: 15px 20px 25px;\n  }\n}\n\n.main-card__img {\n  width: 100%;\n  height: 400px;\n  @include icon-background;\n\n  @include for-tablet {\n    margin-top: -70px;\n    height: 300px;\n    display: none;\n  }\n}\n\n.theme_dark .main-card__img {\n  opacity: 0.8;\n}\n\n.main-card_advantages .main-card__img {\n  background-image: url(../../assets/svg/advantages.svg);\n\n  @include for-tablet {\n    display: none;\n  }\n}\n.main-card_textbook .main-card__img {\n  background-image: url(../../assets/svg/textbook.svg);\n}\n.main-card_games .main-card__img {\n  background-image: url(../../assets/svg/games.svg);\n}\n.main-card_stats .main-card__img {\n  background-image: url(../../assets/svg/stats.svg);\n}\n.main-card_about .main-card__img {\n  background-image: url(../../assets/svg/about.svg);\n}\n\n.accordion .main-card .main-card__link {\n  display: none;\n}\n\n.accordion .main-card .main-card__description {\n  position: absolute;\n  width: 100%;\n  min-height: 320px;\n  bottom: 0px;\n  left: 0px;\n  padding: 20px 30px 50px;\n  opacity: 0;\n  background-color: $whiteOpacity;\n  @include trans-medium;\n\n  @include for-tablet {\n    min-height: 100px;\n    padding: 15px 20px 15px;\n  }\n}\n\n.theme_dark .accordion .main-card .main-card__description {\n  color: $textLight;\n  background-color: $blackOpacity;\n}\n\n.accordion .main-card .main-card__description:hover {\n  background-color: $whiteOpacityHover;\n  @include trans-medium;\n}\n\n.theme_dark .accordion .main-card .main-card__description:hover {\n  background-color: $blackOpacityHover;\n}\n\n.accordion .main-card .main-card__description h2 {\n  margin-bottom: 10px;\n  text-transform: uppercase;\n  font-style: normal;\n  font-weight: bold;\n  letter-spacing: 1px;\n  font-size: 45px;\n  color: #444;\n  text-align: left;\n  text-shadow: -1px -1px 1px #ccc;\n\n  @include for-tablet {\n    font-size: 35px;\n  }\n}\n\n.theme_dark .accordion .main-card .main-card__description h2 {\n  color: $textColor;\n  text-shadow: -1px -1px 1px #333;\n}\n\n.main-card__description .description__item {\n  @include flex-start;\n}\n\n.main-card__description .description__icon {\n  width: 18px;\n  margin-right: 10px;\n}\n\n.main-card__description .description__text {\n  text-align: end;\n}\n\n.description__list {\n  margin-top: 15px;\n}\n\n.main-card__title span {\n  font-family: Ribeye, Arial;\n  font-size: 4rem;\n  color: $white;\n\n  @include for-tablet {\n    font-size: 3.2rem;\n  }\n}\n.accordion .main-card .main-card__title {\n  margin-top: -35px;\n  margin-bottom: 20px;\n\n  @include for-tablet {\n    margin-top: 34px;\n    margin-bottom: 8px;\n  }\n}\n\n.main-card__inner .main-card__title {\n  & .letter0 {\n    color: $brandedColor5light;\n  }\n  & .letter1 {\n    color: $brandedColor7;\n  }\n  & .letter2 {\n    color: $brandedColor2;\n  }\n  & .letter3 {\n    color: $brandedColor4;\n  }\n  & .letter4 {\n    color: $brandedColor1light;\n  }\n  & .letter5 {\n    color: $brandedColor3;\n  }\n}\n\n.theme_dark .main-card__inner .main-card__title {\n  & .letter2 {\n    color: $brandedColor2light;\n  }\n  & .letter5 {\n    color: $brandedColor3light;\n  }\n}\n\n.accordion .main-card .advantages__title {\n  font-size: 1.2rem;\n}\n\n.advantages {\n  margin-top: 34px;\n}\n.accordion .main-card .advantages__title {\n  font-size: 1.4rem;\n  margin-bottom: 20px;\n  color: $brandedColor3;\n\n  @include for-tablet {\n    margin-bottom: 10px;\n    font-size: 1.3rem;\n  }\n}\n\n.accordion .main-card .advantage__title {\n  padding-left: 40px;\n  font-size: 1.2rem;\n  position: relative;\n  margin-bottom: 10px;\n  margin-top: 24px;\n}\n\n.accordion .main-card .advantage__text,\n.accordion .main-card .about-project {\n  font-size: 1rem;\n}\n\n.accordion .main-card .about-project {\n  max-width: 80%;\n\n  @include for-tablet {\n    max-width: 100%;\n  }\n}\n\n.advantage .advantage__title::before {\n  position: absolute;\n  top: 0;\n  left: 0;\n  content: '';\n  width: 30px;\n  height: 30px;\n  @include icon-background;\n}\n\n.advantage__words .advantage__title::before {\n  background-image: url(../../assets/svg/adv-words.svg);\n}\n\n.advantage__games .advantage__title::before {\n  background-image: url(../../assets/svg/adv-games.svg);\n}\n\n.advantage__stats .advantage__title::before {\n  background-image: url(../../assets/svg/adv-stats.svg);\n}\n","@mixin cards-shadow {\n  -moz-box-shadow: 1px 5px 15px #555;\n  -webkit-box-shadow: 1px 5px 15px #555;\n  box-shadow: 1px 5px 15px #555;\n}\n\n@mixin cards-shadow-dark {\n  -moz-box-shadow: 1px 5px 15px #222;\n  -webkit-box-shadow: 1px 5px 15px #222;\n  box-shadow: 1px 5px 15px #222;\n}\n\n@mixin trans-fast {\n  -webkit-transition: 0.3s ease-out;\n  -moz-transition: 0.3s ease-out;\n  -o-transition: 0.3s ease-out;\n  transition: 0.3s ease-out;\n}\n\n@mixin trans-medium {\n  -webkit-transition: 0.5s ease-out;\n  -moz-transition: 0.5s ease-out;\n  -o-transition: 0.5s ease-out;\n  transition: 0.5s ease-out;\n}\n\n@mixin trans-slow {\n  -webkit-transition: 1s ease-out;\n  -moz-transition: 1s ease-out;\n  -o-transition: 1s ease-out;\n  transition: 1s ease-out;\n}\n\n@mixin icon-background {\n  background-size: contain;\n  background-position: center;\n  background-repeat: no-repeat;\n}\n\n@mixin flex-start {\n  display: flex;\n  justify-content: start;\n  align-items: center;\n}\n\n@mixin flex-center {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: center;\n  align-items: center;\n}\n\n@mixin flex-column-start {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n}\n\n@mixin flex-column-center {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n@mixin flex-between {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: space-between;\n  align-items: center;\n}\n\n@mixin flex-evenly {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: space-evenly;\n  align-items: center;\n}\n\n@mixin for-laptop {\n  @media (max-width: 1600px) {\n    @content;\n  }\n}\n\n@mixin for-laptop-down {\n  @media (max-width: 1200px) {\n    @content;\n  }\n}\n\n@mixin for-tablet {\n  @media (max-width: 720px) {\n    @content;\n  }\n}\n\n@mixin for-mobile {\n  @media (max-width: 600px) {\n    @content;\n  }\n}\n","$primaryColor: #4e97c2;\n$secondaryColor: #026aa7;\n$darkColor: #284968;\n$lightColor: #95bdd4;\n$backgroundColor: #f7f9ff;\n$backgroundDark: #111f2e;\n$textColor: #ecf7ff;\n$textDark: #000c18;\n$textLight: #8ad4ff;\n$white: #fff;\n$greyColor: #a2a6ad;\n$blackOpacity: rgba(0, 0, 0, 0.7);\n$blackOpacityHover: rgba(0, 0, 0, 0.8);\n$whiteOpacity: rgba(255, 255, 255, 0.8);\n$whiteOpacityHover: rgba(255, 255, 255, 0.9);\n\n$brandedColor1: #8b0061;\n$brandedColor2: #1b73ba;\n$brandedColor3: #f8d23c;\n$brandedColor4: #ff534d;\n$brandedColor5: #de8a00;\n$brandedColor6: #7478ae;\n$brandedColor7: #8cdd3f;\n\n$brandedColor1dark: #4b0034;\n$brandedColor2dark: #0d395e;\n$brandedColor3dark: #a38a27;\n$brandedColor4dark: #812a27;\n$brandedColor5dark: #9b6001;\n$brandedColor6dark: #4b4d70;\n$brandedColor7dark: #578b25;\n\n$brandedColor1light: #b90082;\n$brandedColor2light: #2189df;\n$brandedColor3light: #e6c337;\n$brandedColor4light: #ff534d;\n$brandedColor5light: #ff9d00;\n$brandedColor6light: #7478ae;\n$brandedColor7light: #8cdd3f;\n\n$mainCardWidth: 45%;\n"],"sourceRoot":""}]);
